<template>
  <router-view :curr-symbol="network.type.name" :get-balance="getBalance" :get-txs="getTxs"/>
</template>

<script>
import { mapGetters } from 'vuex';
import ethplorer from '@/services/ethplorer.js';
import BigNumber from 'bignumber.js';

export default {
  computed: {
    ...mapGetters({
      account: 'account',
      web3: 'web3',
      network: 'network'
    })
  },
  mounted() {},
  methods: {
    async getBalance() {
      if (this.account.address) {
        const balance = await this.web3.eth.getBalance(
          this.account.address.toLowerCase()
        );
        return this.web3.utils.fromWei(
          new BigNumber(balance).toFixed(),
          'ether'
        );
      }
      return '';
    },
    async getTxs() {
      return await ethplorer.getEthTx(this.account.address);
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'EthWalletLayout.scss';
</style>
