import unit from 'ethjs-unit';

class Ethplorer {
  constructor() {
    this.apiRoute = 'https://api.ethplorer.io';
  }

  async _sendReq(req) {
    await sleep(2000);
    const res = await fetch(req);
    return res;
  }

  async get(endpoint, params) {
    const url = `${this.apiRoute}${endpoint}${
      params ? '?' + objToQueryString(params) : ''
    }`;

    const req = new Request(encodeURI(url), {
      method: 'GET'
    });

    const res = await this._sendReq(req);

    if (!res.ok) throw new Error(res.statusText);

    return await res.json();
  }

  //routes
  async getTokenTx(
    address,
    params = {
      apiKey: 'freekey',
      token: '0x8424EB7B1E2B8a0254FA7B16df358f11c4572c13',
      type: 'transfer'
    }
  ) {
    const txs = await this.get(`/getAddressHistory/${address}`, params);
    return txs.operations.map(tx => formatTokenTx(tx));
  }

  async getEthTx(
    address,
    params = {
      apiKey: 'freekey'
    }
  ) {
    const txs = await this.get(`/getAddressTransactions/${address}`, params);
    return txs.map(tx => formatEthTx(tx));
  }
}

async function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

function objToQueryString(obj) {
  return Object.keys(obj)
    .map(key => key + '=' + obj[key])
    .join('&');
}

function formatTokenTx(tx) {
  tx.value = unit.fromWei(tx.value, 'ether').toString();
  tx.symbol = tx.tokenInfo.symbol;
  tx.hash = tx.transactionHash;
  return tx;
}

function formatEthTx(tx) {
  tx.symbol = 'ETH';
  return tx;
}

export default new Ethplorer();
